body {
  margin: 0;
  /* font-family: 'Oxanium', cursive; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/static/media/3.6f5e3691.png) no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html {
  font-size: calc(60% + 0.8vmin) !important;
}

.custom-button::after, .custom-button::before {
  height: calc(100% - 4px);
  width: 100%;
  display: block;
  position: absolute;
  left: 0;
  content: "";
  border-radius: 14px;
}
.custom-button:before {
  bottom: 0;
  background: linear-gradient(#fd9c00,#fd9925);
  z-index: -2;
}
.custom-button:after {
  top: 0;
  background: linear-gradient(#fd9c00,#fdc325);
  z-index: -1;
}
.custom-button {
  height: 60px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  color: #fff;
  -webkit-filter: drop-shadow(0 0 12px rgba(255,68,23,.56));
  filter: drop-shadow(0 0 12px rgba(255,68,23,.56));
  position: relative;
  z-index: 1;
  border-radius: 14px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 15px 23px;
  line-height: 1;
  font-family: "Oxanium",cursive;
  font-weight: 500;
}


@font-face {
    font-family: 'Oxanium';
    src: url(/static/media/grind_demolished-webfont.3e09c999.woff2) format('woff2');
    font-weight: normal;
    font-style: normal;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.sticky {
  left: 0;
  margin: auto;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  /* background-image: url(../img/bg/header_bg.jpg); */
  -webkit-animation: 1000ms ease-in-out 0s normal none 1 running fadeInDown;
  animation: 1000ms ease-in-out 0s normal none 1 running fadeInDown;
  box-shadow: 0 10px 15px rgba(25, 25, 25, 0.1);
  border-radius: 0;
  padding: 0;
}

.customModal {
  background: #6e43ef!important;
  text-align: center!important;
  width: 770px;
}
.customModal button {
  width: 160px;
  margin-top: 5vh;
}
.customModal p {
  font-size: 35px;
  color: white;
  padding: 10px;
}
.modal-img {
  width: 90px;
  margin-top: 3vh;
  margin-bottom: 2vh;
}
.modal-button {
  background: hsl(41deg 98% 49%);
  padding: 10px 20px;
  font-size: 20px;
  width: 100px;
  border: 10px;
  color: white;
  font-family: 'Oxanium';
  font-weight: 500;
  box-shadow: 2px 3px #4a32cd;
  border-radius: 10px;
}
